import React, {Component} from 'react';
import { Card, CardImg, CardBody, CardText, CardTitle, Breadcrumb, BreadcrumbItem, Button,
    Modal, ModalHeader, ModalBody, Label, Col, Row } from 'reactstrap';
import { Link } from 'react-router-dom';
import {Control, LocalForm, Errors } from 'react-redux-form';
import {Loading} from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';


function RenderDish({dish}) {
    
    return (
    <FadeTransform
        in
        transformProps={{
            exitTransform: 'scale(0.5) translateY(-50%)'
    }}>
        <Card>
            <CardImg top src={baseUrl + dish.image} alt={dish.name} />
            <CardBody>
                <CardTitle>{dish.name}</CardTitle>
                <CardText>{dish.description}</CardText>
            </CardBody>
        </Card>
    </FadeTransform>

    );
}

const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => (val) && (val.length >= len);

class CommentForm extends Component 
{
      
    constructor(props){
        super(props);

        this.state = {
            modalOpen: false
        }

        this.toggleModal = this.toggleModal.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);

    }

    toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }
    
    handleSubmit(values) {
        this.toggleModal();
        this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);    
    }
    

    render(){
        return (
            <div>
                <Button onClick={this.toggleModal} outline>
                    <span class="fa fa-pencil"></span> Submit Comment
                </Button>
                
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
                    <ModalBody >
                        <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                            
                            <Row className="form-group">
                                <Label className="col-12">
                                    Rating
                                </Label>
                                <Col className="col-12">
                                    <Control.select model=".rating" name="contactType" 
                                    className="form-control" >
                                        <option>1</option>
                                        <option>2</option>
                                        <option>3</option>
                                        <option>4</option>
                                        <option>5</option>
                                    </Control.select>
                                </Col>
                            </Row>
                            
                            <Row className="form-group">
                                <Label htmlFor="lastname" className="col-12">Your name</Label>
                                <Col className="col-12">
                                    <Control.text model=".author" id="name" 
                                    name="name" 
                                    placeholder="Enter your name"
                                    className="form-control"
                                    validators={{
                                        minLength: minLength(3), maxLength: maxLength(15)
                                    }}/>
                                    <Errors 
                                    className="text-danger"
                                    model=".author"
                                    show="touched"
                                    messages={{
                                        minLength: 'Must be greater than 2 charecters',
                                        maxLength: "Enter atleats 15 charectors"
                                    }}
                                    />
                                </Col>
                            </Row>
                            
                            <Row className="form-group">
                                <Label htmlFor="comment" className="col-12"> Comment </Label>
                                <Col className="col-12" >
                                    <Control.textarea model=".comment" id="comment" 
                                    name="comment" 
                                    rows="6"
                                    className="form-control" 
                                    />         
                                                           
                                </Col>
                            </Row>

                            <Row className="form-group">
                                <Col className="col-auto">
                                    <Button type="submit" color="primary">
                                        Submit
                                    </Button>
                                </Col>
                            </Row>
                        
                        </LocalForm>
                    </ModalBody>
                </Modal>
            </div>
        )
    }  
}

function RenderComments({comments , postComment , dishId}) {
    
    const DishComments = comments.map((individualcomment) => {
        return (
            <Stagger in>
                <Fade in>
                    <div key={individualcomment.id}>
                        <br />
                        <p>
                            {individualcomment.comment}
                            <br />
                            -- {individualcomment.author} , {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(individualcomment.date)))}
                        </p>
                    </div>
                </Fade>    
            </Stagger>

        );
    });

    return (
        <div className='col-12 col-md-5 m-1'>
            <h4>
                Comments
            </h4>
            {DishComments} 
            <CommentForm dishId = {dishId} postComment = {postComment} />
        </div>

    );

}

        

const DishDetail = (props) => {

    if(props.isLoading) {
        return (
            <div className="conatainer">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
    }
    else if (props.errMess) {
        return (
            <div className="conatainer">
                <div className="row">
                    <h4>
                        {props.errMess}
                    </h4>
                </div>
            </div>
        );
 
    }
    else 
    { 
        const dish = props.dish;

        if(dish!=null) 
        {
        
            return(
                <div className='container'>

                    <div className="row">
                        <div className="col-12">    
                            <Breadcrumb>
                                <BreadcrumbItem><Link to='/menu'> Menu </Link></BreadcrumbItem>
                                <BreadcrumbItem active> {props.dish.name}  </BreadcrumbItem>
                            </Breadcrumb>
                        </div>
                        <div className="col-12">
                            <h3> {props.dish.name} </h3>
                            <hr />
                        </div>
                    </div>

                    <div className="row">
                        <div className="col-12 col-md-5 m-1">
                            <RenderDish dish = {props.dish} />
                        </div>
                        <RenderComments comments = {props.comments} 
                        postComment={props.postComment}
                        dishId={props.dish.id} //this is the id of dish for which the comment is added which was the last property left unassigned fro the comment object.
                        />
                    </div>
                </div>             
            )
        }
        else {
            return(
                <div></div>
            )
        }
    }
}

export default DishDetail;