import React from 'react';
import { Card, CardImg, CardImgOverlay, CardTitle, Breadcrumb, BreadcrumbItem } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';

    //it could have been RendrMenuItems(props) directly but we know what the props are hence i could specify it this way aswell. 
    function RenderMenuItem({ dish, onClick }) {
        return(
            <Card>
                <Link to={`/menu/${dish.id}`} >
                    <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name} />
                    <CardImgOverlay>
                        <CardTitle>
                            {dish.name}
                        </CardTitle>
                    </CardImgOverlay>                       
                </Link>
            </Card>

        );
    }   

    //second way of making a fucntion using arrow function of ES6
    const Menu = (props) => {
        
        //no need for this. as functional components only have props
        const menu = props.dishes.dishes.map((dish) => {
            return (
                <div key={dish.id} className="col-12 col-md-5 m-1">
                    <RenderMenuItem dish={dish} />
                </div>
            );
        });
        
        if (props.dishes.isLoading) {
            return (        
            <div className="conatainer">
                <div className="row">
                    <Loading />
                </div>
            </div>  
            );
        }

        
        else if (props.dishes.errMess) {
            return (
                <div className="conatainer">
                    <div className="row">
                        <h4>
                            {props.errMess}
                        </h4>
                    </div>
                </div>
            );
        }

        else
        {
            return ( 
                <div className="container">
                    <div className="row">
                        <div className="col-12">    
                            <Breadcrumb>
                                <BreadcrumbItem><Link to='/home'>Home </Link></BreadcrumbItem>
                                <BreadcrumbItem active>Menu</BreadcrumbItem>
                            </Breadcrumb>
                        </div>
                        <div className="col-12">
                            <h3>Menu</h3>
                            <hr />
                        </div>
                    </div>
                    <div className="row">
                        {menu}
                    </div>
                </div>
            );
        }
    }

export default Menu;